@media screen and (max-width: 1550px) and (min-width: 1001px) {  


    .craftView {
        width: 100%;
        height: 100vh;
    }
    
    .craftView_middle {
        width: 100%;
        height: 80%;
        background-image: url("../../../image_assets/general/background_image.webp");
        background-repeat: no-repeat;
        background-size: cover;
        display: flex;
        justify-content: center; 
    }
    
    .craftView_middle_container {
        width: 1024px;
        height: 100%;
        background: rgb(190,146,116);
        background: radial-gradient(circle, rgba(190,146,116,1) 0%, rgba(109,89,76,1) 100%);
        z-index: 100;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .craftView_extra_space_top {
        height: 20px;
        width: 100%;
    }
    
    .craftView_extra_space_bottom {
        height: 60px;
        width: 100%;
    }
    
    
    .craftView_middle_ingred_container::-webkit-scrollbar {
        display: none; /* for Chrome, Safari, and Opera */
    }
    


    
    .craftView_footer {
        width: 100%;
        height: 10%;
        background-color: #2e1e12;
        display: flex;
        justify-content: center;
        position: relative;
        z-index: 200;
    } 
    
    .craftView_footer_container {
        width: 500px;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 5px;
        position: absolute;
        
    }
    
  
    .recipe_ingredient:hover {
        scale:0.95;
    }
    
    .footer_cauldrun {
        height: 200%; 
        transform: translateY(-40px); 
        cursor: pointer;
        opacity: 0.5;
        pointer-events:none;
        cursor: pointer;
        transition: all .2s ease-in-out;
    }
    .footer_cauldrun:hover {
        scale:1.05;
        transform: translateY(-55px);
    }
    



    .craftView_middle_container {
        width: 700px;
        height: 100%;
        background: rgb(190,146,116);
        background: radial-gradient(circle, rgba(190,146,116,1) 0%, rgba(109,89,76,1) 100%);
        z-index: 100;
        display: flex;
        justify-content: center;
        align-items: center;


    }

    .craftView_middle_ingred_container {
        width: 315px;
        height: 100%;
        display: flex;
        flex-wrap: wrap;
        gap: 5px;
        row-gap: 0px;
        overflow: scroll;
        -ms-overflow-style: none;
        scrollbar-width: none;
        overflow-y: scroll;
    }

    .craftView_ingredBox {
        width: 75px;
        height: 78px;
        position: relative;
        text-align: center;
        cursor: pointer;
    }

    .craftView_ingred_amount {
        width: 20px;
        position: absolute;
        top: 68.8%; /* 65% previous */
        left: 65.4%;
        color: white;
        font-family: 'Luckiest Guy', cursive;
        font-size: 10px;
        margin: 0;
    }

    .craftView_ingred {
        width: 75px;
        height: 75px;
    }

    .recipe_ingredient {
        height: 95%;
        transition: all .2s ease-in-out;
    }

    .craftView_footer_craftholder {
        width: 60px;
        height: 100%;
        position: relative;
        text-align: center;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .selected_ingredients_amount {
        width: 20px;
        text-align: center;
        color: white;
        font-size: 9px;
        font-weight: bold;
        font-family: 'Luckiest Guy', cursive;
        position: absolute;
        top: 54%;
        left: 64%;
        display: none;
        justify-content: center;
    }







    /*    CRAFTPOTION     */





    .craftPotion {
        width: 100%;
        height: 100vh;
        background-color: rgba(0,0,0,0.7);
        position: absolute;
        top: 0;
        left: 0;
        z-index: 500;
        display: flex;
        justify-content: center;
        align-items: center;
        display: none;
        
    }
    
    
    #revealCard_text {
        margin: 0;
        font-family: 'Luckiest Guy', cursive;
        font-size: 30px;
        color: white;
    }
    
    .craftPotion_imageholder {
        width: 100%;
        height: 65%;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .craftPotion_image {
        height: 100%;
        cursor: pointer;
        opacity: 0.2;
    }
    

    .craftPotion_contentcontainer {
        width: 318px;
        height: 456px;
        transform: translateY(-80px);
        background-image: url("../../../image_assets/general/potion_reveal_background.webp");
        background-repeat: no-repeat;
        background-size: cover;
        border-radius: 7px;
    }

    

    .craftPotion_textholder {
        width: 100%;
        height: 14%;
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
    }
    .craftPotion_textholder_exitBtn {
        width: 40px;
        position: absolute;
        left: 84.5%;
        cursor: pointer;
    }


    .mainContainer {
        position: relative;
        width: 200px;
        height: 275px;
        cursor: pointer;
    }

    .craftPotion_ingredientsholder {
        width: 100%;
        height: 23%;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .craftPotion_ingred {
        height: 90%;
    }

    .craftPotion_ingredientwrapper {
        width: 72px;
        height: 80px;
        position: relative;
        text-align: center;
    }

    .craftPotion_amount_text {
        color: white;
        font-size: 10px;
        font-weight: bold;
        position: absolute;
        top: 59%;
        left: 64%;
        margin: 0;
        text-align: center;
        width: 20px;
    }




    /* FLIP CARD */


    .theFront {
        position: absolute;
        width: 100%;
        height: 100%;
        backface-visibility: hidden;
        color: grey;
    }
    .theFront>img {
        width: 200px;
    }
    
    .theBack {
        position: absolute;
        width: 100%;
        height: 100%;
        backface-visibility: hidden;
        color: #333;
        transform: rotateY(180deg);
    }
    .theBack>img {
        width: 200px;
    }




    .mainContainer {
        position: relative;
        width: 200px;
        height: 285px;
        cursor: pointer;
    }
    
    .theCard {
        position: absolute;
        width: 100%;
        height: 100%;
        transform-style: preserve-3d;
        transition: all 1s ease;
    }




}


